<template>
  <div id="singleEcg_" :ecgList='ecgList' :closeTime="closeTime">
      <canvas class="myCanvas" id="myCanvas" width="1080" height="379"></canvas>
      <div class="tpec-btn type-tab-btn jc-active" @click='chooseJc'>
        <i class="iconfont tpec-img">&#xe621;</i>
        <span>{{$t('monitor.fz')}}</span>
      </div>
      <div class="mask" v-if="showData">
        {{$t('message.noData')}}
      </div>
  </div>
</template>

<script>
import { setTimeout, setInterval } from 'timers';
export default {
  name: 'heart',
  data () {
    return {
      ecgLists:[],//接受websockt传的点
      newArrs:[],//记录画了多少个点
      list:[],//将要画的点
      cheke:false,//是否要多画点
      moreLength:0,//一秒画后剩余了多少个点
      closeTimes:true,//单纯的强制停止画图或者重新启动画图
      fanzhuan:false,//是否翻转
      showData:true
    }
  },
  props: {
    ecgList:Array,
    closeTime:Boolean
   },
  watch:{
    ecgList(curVal,oldVal){
        if(curVal.length>oldVal.length){
            this.showData = false
            var newVal = curVal.slice(oldVal.length,curVal.length)//保证每次为一秒128个数据
            if(this.list.length>0){//如果有点一秒内没有画完 下次执行的时候重新多画
              this.moreLength = this.list.length+8
              this.cheke = true
            }else{//否则按照8个点一画
              this.cheke = false
            }
            // this.moreLength = this.list.length-newVal.length+128
            this.list = this.list.concat(newVal)
            // if((this.moreLength)>0){
            //        this.cheke = true
            // }
            // console.log(this.moreLength)
            // console.log(this.list)
        }
    //  if(curVal){
    //    this.ecgLists=curVal;
    //    var newVal = curVal.splice(0,oldVal.length)
    //    console.log(newVal)
    //   }
   },
   closeTime(curVal){
       // console.log(curVal,"删除")
      this.closeTimes = curVal
      this.ecgLists = [];
      this.newArrs = [];
      this.list = [];
      if(this.closeTimes){
        this.heart();
      }
   }
  },
  mounted(){
    // var canvas = document.getElementById("myCanvas");
    // if(canvas){
    //
    // }else{
    //
    // }
    this.heart();
    this.drawigrod()
    // console.log(this.closeTimes)
  },
  updated(){
      console.log("更新")
      console.log(this.closeTimes)
      if(this.closeTimes){
        this.heart();
      }
  },
  methods:{
    heart(){
      var nums = new this.unitConversion().mmConversionPx(25);
    //   this.length = this.ecgLists.length
    //   var list = this.ecgLists
    //   console.log(this.length,"4")
    //   console.log(list)
      var current_time_millis = 0;
      var that = this
      function loop() {
        var A = new Date().getTime();
        current_time_millis = A;
        b(that)
        // console.log(that.list.length,"10")
         if(that.closeTimes&&(that.list.length>0)){
              setTimeout(loop,62.5);
              var C = new Date().getTime();
              var B = C - current_time_millis + 1;
              if (B < 62.5) {
                      sleep(62.5 - B)
              }
          }
      }
      loop()
      function sleep(B) {
          var A = new Date().getTime();
          for (var C = 0; C < 10000000; C++) {
              if ((new Date().getTime() - A) > B) {
                  break
              }
          }
      }
      function b(that){
        // console.log(that.cheke)
        // console.log(that.moreLength)
        if(that.cheke){//如果有点没有画完
            var arrs = that.list.splice(0,that.moreLength);
            that.cheke = false
        }else{
            var arrs = that.list.splice(0,8);
        }
        if(that.newArrs.length>=1280){
            that.newArrs = [];
            that.clearCanvas();
            that.drawigrod();
            that.$emit("trun",true)
        }
        that.newArrs = that.newArrs.concat(arrs)//记录画了多少个点，10秒清空一次
        if(document.getElementById("myCanvas")){
          draw(nums,that)
        }else{
          that.closeTimes = false
          that.ecgLists = [];
          that.newArrs = [];
          that.list = [];
        }
        //  if(document.getElementById("myCanvas")){
        //    draw(nums,that)
        //  }
      }
      function draw(num,that)
      {
          var fecg_data = new Array();
          var newArr = that.newArrs;
          var canvas = document.getElementById("myCanvas");
          var ctx = canvas.getContext("2d");
          let width = canvas.width,height=canvas.height;
          canvas.style.width = width + "px";
          canvas.style.height = height + "px";
          canvas.height = height * 1;
          canvas.width = width * 1;
          ctx.scale(1, 1);
          that.drawigrod()
          newArr.forEach((v,i)=>{
              var arr = [];
              arr[0]=i*(num/128)
              if(that.fanzhuan){
                 arr[1]=-v
              }else{
                arr[1]=v
              }
              fecg_data.push(arr)
          })
          var num = num.toFixed(1)
            for (var i = 3; i < fecg_data.length; i++)
              {
                  var x0 = parseInt(fecg_data[i-1][0]) + 50;
                  var x1 = parseInt(fecg_data[i][0]) + 50;
                  var y0 = 100 - 100 * parseFloat(fecg_data[i-1][1]);
                  var y1 = 100 - 100 * parseFloat(fecg_data[i][1]);
                  ctx.strokeStyle = "rgb(0,0,128)";
                  ctx.lineWidth = 1;
                  ctx.beginPath();
                  ctx.moveTo(x0,y0+90);
                  ctx.lineTo(x1,y1+90);
                  ctx.stroke();
              }
      };

    },
    updataData(){
        // console.log(this.ecgLists)
        // if(this.ecgLists.length!=0){
        //     if(this.topp){
        //         // var lengths = this.ecgLists.length
        //         // if((this.length<=1280)&&(this.ecgLists.length!=0)){

        //         // }else{
        //         //     this.length = 0
        //         // }
        //         this.heart();
        //     }
        // }
        // console.log(this.topp)
    },
    drawigrod(){
        var num = new this.unitConversion().mmConversionPx(25).toFixed(1);
        var canvas = document.getElementById("myCanvas");
        var ctx = canvas.getContext("2d");
        if (canvas.getContext) {
               var offsetx = 50;
               var offsety = 0;
               var width = num*10;
               var height = num*4;
               ctx.fillStyle = "rgba(255,255,255,1)";
               ctx.fillRect (offsetx, offsety, width, height);

               var d = num/5;
               for (var i = 0; i <= width/d; i++)
               {
                    if(i%5 == 0){
                        ctx.strokeStyle = "rgba(255,0,0,.6)";
                        ctx.lineWidth = 2;
                    }else{
                        ctx.strokeStyle = "rgba(255,0,0,.3)";
                        ctx.lineWidth = 1;
                    }
                    ctx.beginPath();
                    ctx.moveTo(i*d+offsetx,offsety);
                    ctx.lineTo(i*d+offsetx,height+offsety);
                    ctx.stroke();
               }
               for (var j = 0; j <= height/d; j++)
               {
                    if (j%5 == 0){
                         ctx.strokeStyle = "rgba(255,0,0,.6)";
                         ctx.lineWidth = 2;
                    }else{
                         ctx.strokeStyle = "rgba(255,0,0,.3)";
                         ctx.lineWidth = 1;
                    }
                    ctx.beginPath();
                    ctx.moveTo(offsetx,j*d+offsety);
                    ctx.lineTo(width+offsetx,j*d+offsety);
                    ctx.stroke();
                }
                ctx.font="22px PingFangSC-Medium,PingFang SC";
                ctx.fillStyle = "rgba(38,38,38,1)";
                // ctx.fillText("mm",offsetx+width/2,220);
                // ctx.fillText("0",offsetx,220);
                // ctx.fillText("250",offsetx+width*49/50,220);

                // ctx.fillText("1.0mv",0,11);
                // ctx.fillText("10mm/mV",420,250);
                // ctx.fillText("25mm/s",580,250);
                // ctx.fillText("-1.0mv",0,200);
        }
    },
    clearCanvas()
    {
        var canvas = document.getElementById("myCanvas");
        var ctx = canvas.getContext("2d");
        canvas.height=canvas.height;
    } ,
    unitConversion() {//像素转厘米
          /**
           * 获取DPI
           * @returns {Array}
           */
          this.conversion_getDPI =function () {
              var arrDPI = new Array;
              if (window.screen.deviceXDPI) {
                  arrDPI[0] = window.screen.deviceXDPI;
                  arrDPI[1] = window.screen.deviceYDPI;
              } else {
                  var tmpNode = document.createElement("DIV");
                  tmpNode.style.cssText = "width:1in;height:1in;position:absolute;left:0px;top:0px;z-index:99;visibility:hidden";
                  document.body.appendChild(tmpNode);
                  arrDPI[0] = parseInt(tmpNode.offsetWidth);
                  arrDPI[1] = parseInt(tmpNode.offsetHeight);
                  tmpNode.parentNode.removeChild(tmpNode);
              }
              return arrDPI;
          };
          /**
           * px转换为mm
           * @param value
           * @returns {number}
           */
          this.pxConversionMm = function (value) {
              var inch = value/this.conversion_getDPI()[0];
              var c_value = inch * 25.4;
      //      console.log(c_value);
              return c_value;
          };
          /**
           * mm转换为px
           * @param value
           * @returns {number}
           */
          this.mmConversionPx = function (value) {
              var inch = value/25.4;
              var c_value = inch*this.conversion_getDPI()[0];
      //      console.log(c_value);
              return c_value;
          }
      },
      chooseJc(){//选择翻转
          this.fanzhuan = !this.fanzhuan
      }
  },
  beforeDestroy(){
      console.log("销毁")
      // console.log(document.getElementById("myCanvas"))
      this.closeTimes = false
      this.ecgLists = [];
      this.newArrs = [];
      this.list = [];
      this.fanzhuan = false
    //   for(var i = 0; i < 9999; i++) {
    //         clearTimeout(i);
    //   }
  }
}
</script>
<style scoped>
  .ecg-content{
    width: 100%;
    height: 100%;
    margin: 0;
    margin-top:200px;
    display: flex;

  }
  #ecgCanvas{
    position:relative;
  }
  #singleEcg_{
      margin: auto;
      top:200px;
  }
  .type-tab-btn{
      display:flex;
      justify-content: space-around;
      padding:6px 16px;
      float:left;
      margin-bottom:5px;
      background:#fff;
      border-radius: 8px;
      border:1px solid #1890FF;
      margin-right:3px;
      cursor: pointer;
      position: absolute;
      right: 22px;
      top: -47px;
  }
  .tpec-img{
    color:#1890FF;
    margin-right:9px;
  }
  .type-tab-btn span{
    font-family: PingFangSC-Medium;
    font-size: 14px;
    color: #1890FF;
  }
  .mask{
    width:276px;
    height:67px;
    line-height:67px;
    background:rgba(0,0,0,1);
    border-radius:6px;
    opacity:0.43;
    font-size:18px;
    font-family:PingFangSC-Medium,PingFang SC;
    font-weight:500;
    color:rgba(255,255,255,1);
    position: absolute;
    top:50%;
    left:50%;
    transform:translate(-50%,-50%);
  }
</style>



