<template>
  <PopUp :flow='tzjcStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='showSet()'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe64b;</i>
          <span class="title-span">{{$t('monitor.tzjc')}}</span>
        </div>
        <div class="content">
          <div class="form">
            <div class="form-item">
              <div class="form-item-left">
                <span>{{$t('monitor.hzxx')}}：</span>
              </div>
              <div class="form-item-right">
                <span>{{transData.patientName}} {{transData.caseNum}}</span>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-left">
                <span>{{$t('monitor.selecttzsb')}}：</span>
              </div>
              <div class="form-item-right">
                <el-select class='select' style='width:145px' v-model='selectVal' filterable @change="selectMachine" :placeholder="$t('message.select')">
                 <el-option
                   v-for="item in machineTypeList"
                   :key="item.id"
                   :label="item.name"
                   :value="item">
                 </el-option>
               </el-select>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-left">
                <span>{{$t('monitor.ysbh')}}：</span>
              </div>
              <div class="form-item-right">
                <span class='ysbh'>{{machineNo}}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='chooseBedType()'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='showSet()'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import {record} from '@/api/monitor'
import { mapState,mapActions} from 'vuex'
  export default {
    name: 'tjzc',
      data(){
          return {
            selectVal: this.value || '',
            machineTypeList:[
            ],
            machineNo:"",
            machineType:""
          }
      },
      props:["tzjcStatus","transData"],
      computed:{
        ...mapState("monitor",["departmentList","selectIndex"]),
        ...mapState("login",["language"]),
      },
      components:{
          'PopUp': popUp,
      },
      methods:{
        ...mapActions("monitor",["getData"]),
        showSet(){
            this.$emit("closeIitem",'停止监测')
        },
        chooseBedType(){
          console.log(this.machineType)
          var that = this
          if(this.machineType ===0 ||this.machineType){
            record({
              baseId:this.transData.baseId,
              type:this.machineType
            }).then((res)=>{
              console.log(res)
              if(res.code==200 && res.success){
                that.$emit("closeIitem",'停止监测')
                that.getData(that.departmentList[that.selectIndex].departmentId)
              }
            })
          }
        },
        filtData(newData){
          var list =[]
          for(var i =0;i<newData.length;i++){
            if(newData[i].usageState ===1){
              if(newData[i].type===0){
                var typeName = ""
                if(this.language=="zh"){
                   typeName = "体温"
                }else{
                   typeName = "Temperature patch"
                }
                list.push({
                  name:typeName,
                  id: newData[i].type,
                  machineNo: newData[i].machineNo
                })
              }else if(newData[i].type===1){
                var typeName = ""
                if(this.language=="zh"){
                   typeName = "心电"
                }else{
                   typeName = "ECG patch"
                }
                list.push({
                  name:typeName,
                  id: newData[i].type,
                  machineNo: newData[i].machineNo
                })
              }else if(newData[i].type===2){
                var typeName = ""
                if(this.language=="zh"){
                   typeName = "血氧"
                }else{
                   typeName = "Oximeter"
                }
                list.push({
                  name:typeName,
                  id: newData[i].type,
                  machineNo: newData[i].machineNo
                })
              }else if(newData[i].type===3){
                var typeName = ""
                if(this.language=="zh"){
                   typeName = "离床感应"
                }else{
                   typeName = "Off-bed inductor"
                }
                list.push({
                  name:typeName,
                  id: newData[i].type,
                  machineNo: newData[i].machineNo
                })
              }
            }
          }
          console.log(list)
          return list
        },
        selectMachine(data){
           this.selectVal = data.name
           this.machineNo = data.machineNo
           this.machineType = data.id
          console.log(data)
        }
      },
      created(){
        console.log(this.transData)
        this.machineTypeList = this.filtData(this.transData.machineList)
      }
  }
</script>
<style scoped lang="scss">
.content{
  margin:0 42px;
  .form{
    width:612px;
    padding-bottom:20px;
    .form-item{
      display:flex;
      height:36px;
      margin-top:24px;
      .form-item-left{
        width:42%;
        span{
          float:right;
          // display:block;
          height:100%;
          display:flex;
          justify-content: center;
          align-items: center;
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:600;
          color:#333;
        }
      }
      .form-item-right{
        width:58%;
        span{
          float:left;
          line-height:36px;
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:#666666;
        }
        .ysbh{
          color:#1890FF;
        }
        .select{
          float:left;
        }
      }

    }
  }
  .msg{
    display:flex;
    span{
      font-size:18px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color:rgba(153,153,153,1);
      text-align: left;
      padding-left:22px;
      margin:18px 0 13px;
    }

  }
  .select-pd{
    padding-left:22px;
    display:flex;
    margin-bottom:14px;
    span{
      font-size:18px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color:rgba(51,51,51,1);
      margin-right:10px;
      line-height:30px;
    }
    a{
      display:inline-block;
      padding:6px 24px;
      border-radius:17px;
      border:1px solid rgba(24,144,255,1);
      color:#1890FF;
      margin-right:22px;
    }
    .lsjl-active{
      background:rgba(24,144,255,1);
      color:#fff;
    }
  }
  .history-tj-abtm{
    padding:16px 0 30px 0;
    width:600px;
    display:flex;
    justify-content: space-between;
    margin:0 auto;
    span{
      font-size:18px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color:rgba(38,38,38,1);
    }
  }
  .history-tj-xlhx{
    width:500px;
    margin:3px auto 0 auto;
    .xlhx-row{
      display:flex;
      justify-content: space-between;
      margin-bottom:16px;
      .ys{
        display:flex;
        justify-content: space-between;
        i{
          width:25px;
          height:1px;
          display:block;
          margin-top: 8px;
        }
        .xl{
          background:#7C6AF2;
        }
        .hx{
          background:#5C89FF;
        }
        span{
          display:block;
          width:60px;
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:rgba(40,45,50,1);
        }
      }
    }
  }
}
</style>



