<template>
  <PopUp :flow='xdjlStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='showSet()'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe64a;</i>
          <span class="title-span">{{$t('monitor.xxxd')}}</span>
        </div>
        <div class="content">
          <div class='msg'>
            <span>{{transData.bedName}} &emsp;{{transData.patientName}}  &emsp;{{transData.caseNum}}  &emsp;{{$t('monitor.ysy')}}：{{transData.monitorHeartRateVO.useTimes}}</span>
            <div class="">

            </div>
          </div>
          <!-- <Heart id='ecgCanvas' :ecgList="ecgList" @trun="showChage" :closeTime="closeTime"></Heart> -->
          <Heart id='ecgCanvas' :ecgList="ecgList" :closeTime="closeTime" @trun="showChage"></Heart>
          <div class="msg-bottom">
            <div class="bottom-warp">
              <div>10 mm/mV</div>
              <div>5 mm/s</div>
            </div>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='showSet()'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='showSet()'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import Heart from '../../components/heart'
var that= {};
  export default {
    name: 'tjzc',
      data(){
          return {
            changemachineId:'',
            machineTypeList:[],
            ws:"",
            ecgList:[],
            closeTime:false,
            saveArr:[],//保存数据
            canChage:false,//看心电组件是否传了数据
            arrs:[]//保存实时传过来的数据
          }
      },
      props:["xdjlStatus","transData"],
      beforeCreate(){
          that = this;
      },
      created(){
         var arr = this.transData.machineList.filter((item)=>{
           return item.type == 1
         })
         console.log(arr)
         if(arr[0].machineId&&arr[0].usageState){
           console.log(arr[0].machineId)
           this.closeTime = true
           var local = window.location.host;
           let ws = new WebSocket('ws://'+local+'/yjjk'+'/websocket/'+arr[0].machineId);
           // let ws = new WebSocket('ws://192.168.31.229:8080/yjjk'+'/websocket/'+arr[0].machineId);
           that.ws = ws;
           that.ws.onmessage = function (e) {
            console.log(e)
            console.log('数据',e.data)
            if(e.data == ''){

            }else{
              if(that.saveArr.length>=384){//保证数据有3s
                var dataArr = that.filtfilt128(that.saveArr)
                that.saveArr = that.saveArr.concat(JSON.parse(JSON.parse(e.data).ecg))//3+1
                that.saveArr.splice(0,128)//删除老一秒的数据 4-1
                console.log(that.saveArr,"22")
                console.log(dataArr)
                that.arrs = that.arrs.concat(dataArr);//+1
                that.ecgList = that.arrs//给心电数据赋值触发watch
              }else{
                that.saveArr = that.saveArr.concat(JSON.parse(JSON.parse(e.data).ecg))
              }
            }
          }
         }
      },
      beforeDestroy(){
        if(this.ws){
           this.ws.close();
        }
      },
      mounted:function(){

      },
      methods:{
          filtfilt128(data){//心电筛选数据
              // Initialize some important filtering parameters and some empty arrays.
            var  x = data
            var  b = [0.000148021986532, 0.000296043973064, 0.000148021986532];
            var  a = [1, -1.965293372622690, 0.965885460568818];
            var  zi= [0.999851978013469, -0.965737438582287];
            var  x_new= [];
            var  y1 = [];
            var  y2 = [];
            var output =[]
              // Pad the data using odd extension.
              for(var i=0; i<127; i++)
              {
              x_new[i] = 2*x[0] - x[127-i];
              }
              for(var i=0; i<384; i++)
              {
              x_new[i+127] = x[i];
              }
              for(var i=0; i<127; i++)
              {
              x_new[i+127+384] = 2*x[383] - x[382-i];
              }

              // Forward filter.
              y1[0] = b[0]*x_new[0] + zi[0]*x_new[0];
              y1[1] = b[0]*x_new[1] + b[1]*x_new[0] - a[1]*y1[0] + zi[1]*x_new[0];
              for(var i=2; i<638; i++)
              {
                y1[i] = b[0]*x_new[i] + b[1]*x_new[i-1] + b[2]*x_new[i-2] - a[1]*y1[i-1] -
                a[2]*y1[i-2];
              }

              // Backward filter.

              y2[637] = b[0]*y1[637] + zi[0]*y1[637];
              y2[636] = b[0]*y1[636] + b[1]*y1[637] - a[1]*y2[637] + zi[1]*y1[637];
              for(var i=635; i>=0; i--)
              {
                y2[i] = b[0]*y1[i] + b[1]*y1[i+1] + b[2]*y1[i+2] - a[1]*y2[i+1] - a[2]*y2[
                i+2];
              }

              // Subtract the result from the original signal.
              // Only the middle 250 values are relevant now.
              for(var i=255; i<383; i++)
              {
              y2[i] = x_new[i] - y2[i];
              }

              for (var i=255;i<(255+128);i++) {
              output[i-255] = y2[i];
              }
              return output
          },
          showSet(){
             this.$emit("closeIitem",'停止心电图')
          },
          // 心电图组件发送过来的数据
          showChage(val){
            console.log("发送了数据")
            this.canChage = val;
            console.log(this.arrs,"1")
            if(this.canChage){//图像画完了清除10秒的数据
                this.arrs.splice(0,1280);
                console.log(this.arrs,"2")
                this.canChage = false
            }
          }
      },
      components:{
          'PopUp': popUp,
          'Heart':Heart,
      }
  }
</script>
<style scoped lang="scss">
.content{
  margin:0 42px;
  .msg{
    display:flex;
    padding-bottom: 10px;
    span{
      font-size:18px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color:rgba(153,153,153,1);
      text-align: left;
      padding-left:22px;
      margin:18px 0 13px;
    }
  }
  .msg-bottom{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 80px;
    width: 100%;
    .bottom-warp{
      display: flex;
      align-items: center;
      width: 50%;
      justify-content: space-between;
      font-size:22px;
      font-weight:500;
      color:rgba(38,38,38,1);
      line-height:30px;
    }
  }
}
</style>



