<template>

  <PopUp :flow='lsjlStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='showSet()'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe649;</i>
          <span class="title-span">{{$t('monitor.lsRecord')}}</span>
        </div>
        <div class="content">
          <div class='msg'>
            <span>{{transData.bedName}}  &emsp;{{transData.patientName}}  &emsp;{{transData.caseNum}}  &emsp;{{$t('monitor.ysy')}}：{{useTime}}</span>
          </div>
          <div class="select-pd">
            <span>{{$t('monitor.xzcjxm')}}</span>
            <a href="javascript:void(0)" :class="pdType==0?'lsjl-active':''" @click='selectPd(0)'>{{$t('monitor.twjl')}}</a>
            <a href="javascript:void(0)" :class="pdType==1?'lsjl-active':''" @click='selectPd(1)'>{{$t('monitor.yyjl')}}</a>
            <a href="javascript:void(0)" :class="pdType==2?'lsjl-active':''" @click='selectPd(2)'>{{$t('monitor.yyhx')}}</a>
          </div>
          <div class="zx-pic" id='myChart' style="width:798px;height:355px;"></div>
          <div class="" v-if='pdType==0'>
            <div class="history-tj-abtm">
              <span>{{$t('monitor.zgwd')}}:<span v-if="!heightTemperature">--</span> {{heightTemperature}}{{sheshi}}</span>
              <span>{{$t('monitor.cxyj')}}: {{warningTimes}} &nbsp;{{$t('monitor.time')}}</span>
            </div>
          </div>
          <div class="" v-if='pdType==1'>
            <div class="history-tj-xy"></div>
          </div>
          <div class="" v-if='pdType==2'>
            <div class="history-tj-xlhx">
              <div class="xlhx-row">
                <div class="ys">
                  <i></i>
                  <span></span>
                </div>
                <span class='ro-center'>{{$t('history.bigest')}}</span>
                <span class='ro-center'>{{$t('history.smallest')}}</span>
                <span class='ro-center'>{{$t('history.average')}}</span>
              </div>
              <div class="xlhx-row">
                <div class="ys">
                  <i class='xl'></i>
                  <span>{{$t('history.xl')}}</span>
                </div>
                <span class='ro-center'>{{huxianshiZhi.xinLvMax}}</span>
                <span class='ro-center'>{{huxianshiZhi.xinLvMin}}</span>
                <span class='ro-center'>{{huxianshiZhi.xinLvAvg}}</span>
              </div>
              <div class="xlhx-row">
                <div class="ys">
                  <i class='hx'></i>
                  <span>{{$t('history.hxl')}}</span>
                </div>
                <span class='ro-center'>{{huxianshiZhi.huxiMax}}</span>
                <span class='ro-center'>{{huxianshiZhi.huxiMin}}</span>
                <span class='ro-center'>{{huxianshiZhi.huxiAvg}}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='comitSet'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='showSet'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import {monitorHistoryData} from '@/api/monitor'
import { mapState,mapActions,mapGetters} from 'vuex'
import popUp from '../../components/popUp'
var that= {};
  export default {
    name: 'tjzc',
      data(){
          return {
            twHeight:32,
            twLow:41,
            twInterval:0.5,
            sheshi:'℃',
            changemachineId:'',
            machineTypeList:[],
            pdType:0,
            temperatureList:[],
            timesList:[],
            heightTemperature:"",//最高温
            warningTimes:0,
            bloodOxygenList:[],
            piList:[],
            bloodTimesList:[],
            tpecTimesList:[],
            xinLvList:[],
            huxiLvList:[],
            huxianshiZhi:{
              huxiMax:"",
              huxiMin:"",
              huxiAvg:"",
              xinLvMax:"",
              xinLvMin:"",
              xinLvAvg:"",
            },
            useTime:""
          }
      },
      props:["lsjlStatus","transData"],
      beforeCreate(){
          that = this;
      },
      created(){
        that.getRul()
        if(!that.selectTem){
          that.twHeight = (((that.twHeight*9)/5) + 32).toFixed(2)
          that.twLow = (((that.twLow*9)/5) + 32).toFixed(2)
          that.sheshi = "℉"
        }
        console.log(this.ruleTwHeight,this.ruleTwLow)
        that.accpetTw()
      },
      mounted:function(){
        that.accpetTw()
      },
      methods:{
        ...mapActions("monitor",["getRul"]),
        showSet(){
            this.$emit("closeIitem",'历史记录')
        },
        comitSet(){
            this.$emit("closeIitem",'历史记录')
        },
        selectPd(type){
          console.log(type)
          that.pdType = type;
          if(type==0){
            that.accpetTw();
          }else if(type==1){
            that.accpetXy();
          }else if(type==2){
            that.accpetXLHX();
          }
        },
        accpetTw(){
          that.useTime = that.transData.monitorTemperatureVO.useTimes
          monitorHistoryData({
            baseId:this.transData.baseId,
            type:0
          }).then((res)=>{
            if(res.code==200&&res.success){
              console.log(res.data)
              var arrTime =[]
              var arrTemperature = []
              that.timesList = []
              res.data.map((item)=>{
                arrTime.push(item.timestamp)
                arrTemperature.push(item.temperature)
              })
              that.temperatureList = arrTemperature
              if(!that.selectTem){//转化为华摄度
                that.temperatureList.forEach((item,index)=>{
                    return  that.temperatureList[index] = (((item*9)/5) + 32).toFixed(2)
                })
              }
              arrTime.map((item)=>{
                  that.timesList.push(that.timestampToTime(item).slice(5,16));
              })
              that.warningTimes = 0
              that.temperatureList.map((item,index,arr)=>{//判断预警次数
                if((Number(arr[index])<=Number(that.ruleTwHeight)) && (Number(that.ruleTwHeight)<Number(arr[index+1]))){
                  console.log("进来1",arr[index])
                  console.log("进来",arr[index+1])
                  that.warningTimes = that.warningTimes+1
                }
              })
              console.log(that.warningTimes)
              if(that.temperatureList.length>0){
                that.heightTemperature = Math.max(...that.temperatureList)
              }
              that.drawLineTW();
            }
          })
        },
        accpetXy(){
          that.drawLineXY();
          that.useTime = that.transData.monitorBloodVO.useTimes
          monitorHistoryData({
            baseId:this.transData.baseId,
            type:2
          }).then((res)=>{
            if(res.code==200&&res.success){
              console.log(res.data)
              var arrTime =[]
              that.bloodOxygenList = []
              that.piList = []
              that.bloodTimesList = []
              res.data.map((item)=>{
                arrTime.push(item.timestamp)
                that.bloodOxygenList.push(item.bloodOxygen)
                that.piList.push(item.pi)
              })
              arrTime.map((item)=>{
                  that.bloodTimesList.push(that.timestampToTime(item).slice(5,16));
              })
              that.drawLineXY();
            }
          })
        },
        accpetXLHX(){
          that.useTime = that.transData.monitorRespiratoryRateVO.useTimes
          monitorHistoryData({
            baseId:this.transData.baseId,
            type:1
          }).then((res)=>{
            if(res.code==200&&res.success){
              console.log(res.data)
              var arrTime =[]
              var filterList = []
              that.xinLvList = []
              that.huxiLvList = []
              that.tpecTimesList = []
              console.log(res.data)
              filterList = res.data.filter((item)=>{
                  return item.heartRate&&item.respiratoryRate
              })
              console.log(filterList)
              filterList.map((item)=>{
                arrTime.push(item.timestamp)
                that.xinLvList.push(item.heartRate)
                that.huxiLvList.push(item.respiratoryRate)
              })
              arrTime.map((item)=>{
                  that.tpecTimesList.push(that.timestampToTime(item).slice(5,16));
              })
              console.log(that.xinLvList)
              console.log(that.huxiLvList )
              console.log(that.tpecTimesList)
              if(that.huxiLvList.length>0){
                that.huxianshiZhi.huxiMax = Math.max(...that.huxiLvList)
                that.huxianshiZhi.huxiMin= Math.min(...that.huxiLvList)
                that.huxianshiZhi.huxiAvg = Math.round(that.average(that.huxiLvList))
              }else{
                 that.huxianshiZhi.huxiMax ="--"
                 that.huxianshiZhi.huxiMin= "--"
                 that.huxianshiZhi.huxiAvg = "--"
              }
              if(that.xinLvList.length>0){
                that.huxianshiZhi.xinLvMax = Math.max(...that.xinLvList)
                that.huxianshiZhi.xinLvMin= Math.min(...that.xinLvList)
                that.huxianshiZhi.xinLvAvg = Math.round(that.average(that.xinLvList))
              }else{
                that.huxianshiZhi.xinLvMax = "--"
                that.huxianshiZhi.xinLvMin= "--"
                that.huxianshiZhi.xinLvAvg = "--"
              }
              that.drawLineXLHX()
            }
          })
        },
        /*绘制图表*/
        average(arr){//求数组平均值
                var len = arr.length;
                var sum = 0;
                for(var i = 0;i<len;i++){
                    sum +=arr[i];
                }
                return Math.floor(sum/len);
        },
        drawLineTW(){
          let myChart = that.$echarts.init(document.getElementById('myChart'));
          myChart.clear();
          myChart.setOption({
             tooltip: {
               trigger: 'axis',
               formatter: "{b}"+"<br>"+"{c}" + that.sheshi,
             },
             grid:{//图标留白区域
               top:'14px',
               left:'60px',
               right:'40px',
               bottom:'33px'
             },
             xAxis: {
                type: 'category',
                boundaryGap: false,
                axisLine:{symbol:['none', 'arrow']},
                data: that.timesList,
                min: that.timesList[0],
                max: that.timesList[that.timesList.length],
                axisLabel: {
                   rotate:0,
                }
              },
              yAxis: {
                  min:that.twLow,
                  max: that.twHeight,
                  interval: that.twInterval,
                  axisLabel: {
                  formatter: '{value}' + that.sheshi
                },
                  axisLine:{symbol:['none', 'arrow']},
              },
              dataZoom: [{
                 start:0,
                 end:50
             }, {
                 type: 'inside',
                 start:0,
                 end:50
             }, {
                 start:0,
                 end:50
             },],
              series: [{
                data: that.temperatureList,
                symbol: 'none',
                sampling: 'average',
                formatter: '{value}' + that.sheshi,
                type: 'line',
                areaStyle: {
                  color: {
                    type: 'linear',
                    x: 1,
                    y: 0,
                    x2: 0,
                    y2: 0,
                    colorStops: [{
                        offset: 0, color: 'rgba(94,119,255,0.65)'
                    }, {
                        offset: 1, color: 'rgba(94,119,255,1)'
                    }],
                    global: false
                }
              },
              lineStyle:{
                color:"rgba(94,119,255,1)",
                width:0
              },
              itemStyle:{
                normal:{
                  borderWidth:0,
                  color:'rgba(94,119,255,1)',
                  borderColor:'rgba(94,119,255,1)',
                }

              },
              markLine:{
                itemStyle: {
                  normal: {
                      borderWidth: 1,
                      lineStyle: {
                          type: 'dotted',
                          width: 2,
                      },
                  },
                },
                data: [
                  {
                      name:"高温",
                      lineStyle: {
                         color:'#E02020'
                     },
                     label: {
                       formatter: this.ruleTwHeight + that.sheshi,
                         textStyle: {
                             fontSize: 16,
                             fontWeight: "bolder"
                         },
                     },
                      yAxis: this.ruleTwHeight
                  },
                  {
                      name:"低温",
                      lineStyle: {
                         color: '#007BB6'
                     },
                     label: {
                       formatter: this.ruleTwLow + that.sheshi,
                         textStyle: {
                             fontSize: 16,
                             fontWeight: "bolder",
                         },
                     },
                      yAxis: this.ruleTwLow
                  },
                ]
              }
            }]
         });
        },
        drawLineXY(){
          let myChart = this.$echarts.init(document.getElementById('myChart'));
          myChart.clear();
          myChart.setOption({
              tooltip: {
               trigger: 'axis',
              },
              grid:{//图标留白区域
                top:'14px',
                left:'60px',
                right:'30px',
                bottom:'33px'
              },
              xAxis: [
                {
                  type: 'category',
                  boundaryGap: false,
                  axisLine:{symbol:['none', 'arrow']},
                  data: that.bloodTimesList,
                  axisLabel: {
                     rotate:0,
                  },
                },
              ],
              yAxis: {
                  min: 0,
                  max: 130,
                  interval: 10,
                  axisLabel: {
                  formatter: '{value}%'
                },
                  axisLine:{symbol:['none', 'arrow']},
              },
              dataZoom: [{
                 start:0,
                 end:50
              }, {
                 type: 'inside',
                 start:0,
                 end:50
              }, {
                 start:0,
                 end:50
              },],
              series: [
                {
                  data: that.bloodOxygenList,
                  name:that.$t('history.xy'),
                  symbol: 'circle',
                  sampling: 'average',
                  formatter: '{value}',
                  type: 'line',
                  lineStyle:{
                    color:"#ff39fa",
                    width:3
                  },
                  itemStyle:{
                    normal:{
                      borderWidth:0,
                      color:'#ff39fa',
                      borderColor:'#7C6AF2',
                    }
                  },
                },
                {
                  data: that.piList,
                  name:that.$t('history.gzzs')+'PI',
                  symbol: 'circle',
                  sampling: 'average',
                  formatter: '{value}',
                  type: 'line',
                  lineStyle:{
                    color:"#7C6AF2",
                    width:3
                  },
                  itemStyle:{
                    normal:{
                      borderWidth:0,
                      color:'rgba(94,119,255,1)',
                      borderColor:'rgba(94,119,255,1)',
                    }
                  },
                }
              ]
           });
        },
        drawLineXLHX(){
          let myChart = this.$echarts.init(document.getElementById('myChart'));
          myChart.clear();
          myChart.setOption({
              tooltip: {
               trigger: 'axis',
              },
              grid:{//图标留白区域
                top:'14px',
                left:'60px',
                right:'30px',
                bottom:'33px'
              },
              xAxis: [
                {
                  type: 'category',
                  boundaryGap: false,
                  axisLine:{symbol:['none', 'arrow']},
                  data: that.tpecTimesList,
                  min: that.tpecTimesList[0],
                  max: that.tpecTimesList[that.tpecTimesList.length],
                  axisLabel: {
                     rotate:0,
                  },
                },
              ],
              yAxis: {
                  min: 0,
                   max: 130,
                   interval: 10,
                  axisLabel: {
                  formatter: '{value}'+that.$t('monitor.ti')
                },
                  axisLine:{symbol:['none', 'arrow']},
              },
              dataZoom: [{
                 start:0,
                 end:50
              }, {
                 type: 'inside',
                 start:0,
                 end:50
              }, {
                 start:0,
                 end:50
              },],
              series:[
                {
                  data: that.xinLvList,
                  name:that.$t('history.xl'),
                  symbol: 'circle',
                  sampling: 'average',
                  formatter: '{value}',
                  type: 'line',
                  lineStyle:{
                    color:"#ff39fa",
                    width:3
                  },
                  itemStyle:{
                    normal:{
                      borderWidth:0,
                      color:'#ff39fa',
                      borderColor:'#7C6AF2',
                    }
                  },
                },
                {
                  data: that.huxiLvList,
                  name:that.$t('history.hxl'),
                  symbol: 'circle',
                  sampling: 'average',
                  formatter: '{value}',
                  type: 'line',
                  lineStyle:{
                    color:"rgba(94,119,255,1)",
                    width:3
                  },
                  itemStyle:{
                    normal:{
                      borderWidth:0,
                      color:'rgba(94,119,255,1)',
                      borderColor:'rgba(94,119,255,1)',
                    }
                  },
                }
              ]
           });
        },
      },
      computed:{
        ...mapGetters("monitor",["ruleTwHeight","ruleTwLow"]),
        ... mapState("monitor",["selectTem"])
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
<style scoped lang="scss">
.content{
  margin:0 42px;
  flex-direction: column;
  .msg{
    display:flex;
    span{
      font-size:18px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color:rgba(153,153,153,1);
      text-align: left;
      padding-left:22px;
      margin:18px 0 13px;
    }

  }
  .select-pd{
    padding-left:22px;
    display:flex;
    margin-bottom:14px;
    span{
      font-size:18px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color:rgba(51,51,51,1);
      margin-right:10px;
      line-height:30px;
    }
    a{
      display:inline-block;
      padding:6px 24px;
      border-radius:17px;
      border:1px solid rgba(24,144,255,1);
      color:#1890FF;
      margin-right:22px;
    }
    .lsjl-active{
      background:rgba(24,144,255,1);
      color:#fff;
    }
  }
  .history-tj-abtm{
    padding:16px 0 30px 0;
    width:600px;
    display:flex;
    justify-content: space-between;
    margin:0 auto;
    span{
      font-size:18px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color:rgba(38,38,38,1);
    }
  }
  .history-tj-xlhx{
    width:500px;
    margin:3px auto 0 auto;
    .xlhx-row{
      display:flex;
      justify-content: space-between;
      margin-bottom:16px;
      .ys{
        display:flex;
        justify-content: space-between;
        i{
          width:25px;
          height:1px;
          display:block;
          margin-top: 8px;
        }
        .xl{
          background:#ff39fa;
        }
        .hx{
          background:#5C89FF;
        }
        span{
          display:block;
          width:60px;
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:rgba(40,45,50,1);
        }
      }
      .ro-center{
        display:block;
        width:100px;
        text-align: center;
      }
    }
  }
}
</style>



