<template>
  <PopUp :flow='qylcgyStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='showSet()'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe651;</i>
          <span class="title-span">{{$t('monitor.qysb')}}-{{$t('monitor.lcgy')}}</span>
        </div>
        <div class="select-row">
          <a href="javascript:void(0)" :class="qyType==0?'select-active':''" @click='qyType=0'>{{$t('monitor.dsbqy')}}</a>
          <!-- <a href="javascript:void(0)" :class="qyType==1?'select-active':''" @click='qyType=1'>批量启用</a> -->
        </div>
        <div class="content" v-if='qyType==0'>
          <div class="content-main">
            <div class="sb-form form">
              <div class="form-item">
                <div class="form-item-name">
                  <span>{{$t('monitor.cwh')}}：</span>
                </div>
                <div class="form-item-value">
                  <el-select class='select' style='width:145px' v-model='formData.bedId' filterable @change="changeBed" :placeholder="$t('monitor.cwh')">
                   <el-option
                     v-for="item in bedList"
                    :key="item.bedId"
                    :label="item.name"
                    :value="item.bedId">
                   </el-option>
                 </el-select>
                </div>
              </div>
              <div class="form-item">
                <div class="form-item-name">
                  <span>{{$t('monitor.sbbh')}}：</span>
                </div>
                <div class="form-item-value">
                  <el-select class='select' style='width:145px' v-model='formData.machineId' filterable :placeholder="$t('monitor.sbbh')" @change='getDevice'>
                   <el-option
                     v-for="item in machineTypeList"
                    :key="item.id"
                    :label="item.value"
                    :value="item.id">
                   </el-option>
                 </el-select>
                </div>
              </div>
              <div class="form-item">
                <div class="form-item-name">
                  <span>{{$t('monitor.hzxx')}}：</span>
                </div>
                <div class="form-item-value">
                  <el-input v-model="formData.patientName" class='select' style='width:145px' :placeholder="$t('monitor.hzxm')"></el-input>
                </div>
              </div>
              <div class="form-item">
                <div class="form-item-name">
                  <span></span>
                </div>
                <div class="form-item-value">
                 <el-input v-model="formData.caseNum" class='select' style='width:145px' :placeholder="$t('monitor.zyh')"></el-input>
                </div>
              </div>
            </div>
            <div class="form-img">
              <img src="../../assets/img/monitor/case3.png" alt="" v-if="lcMachineTypeId==11">
              <img src="../../assets/img/device/htjr.png" alt="" v-if='lcMachineTypeId==15'>
            </div>
          </div>

          <p class='tips'>{{$t('monitor.dyxdk')}}</p>
        </div>
        <!-- <div class="plqy-content" v-if='qyType==1'>
          <div class="plqy-header">
            <div class="plqy-header-item">
              <div class="pl-header-name">床位</div>
              <div class="pl-header-value">设备编号</div>
            </div>
            <div class="plqy-header-item">
              <div class="pl-header-name">床位</div>
              <div class="pl-header-value">设备编号</div>
            </div>
            <div class="plqy-header-item">
              <div class="pl-header-name">床位</div>
              <div class="pl-header-value">设备编号</div>
            </div>
          </div>
          <div class="plqy-main">
            <div class="plqy-main-item">
              <div class="pl-main-name">1901</div>
              <div class="pl-main-value">
                <el-select class='select' style='width:125px' v-model='changemachineId' filterable :placeholder="$t('select')">
                 <el-option
                   v-for="item in machineTypeList"
                   :key="item.machineId"
                   :label="item.str"
                   :value="item.machineId">
                 </el-option>
               </el-select>
              </div>
            </div>
            <div class="plqy-main-item">
              <div class="pl-main-name">1901</div>
              <div class="pl-main-value">
                <el-select class='select' style='width:125px' v-model='changemachineId' filterable :placeholder="$t('select')">
                 <el-option
                   v-for="item in machineTypeList"
                   :key="item.machineId"
                   :label="item.str"
                   :value="item.machineId">
                 </el-option>
               </el-select>
              </div>
            </div>
            <div class="plqy-main-item">
              <div class="pl-main-name">1901</div>
              <div class="pl-main-value">
                <el-select class='select' style='width:125px' v-model='changemachineId' filterable :placeholder="$t('select')">
                 <el-option
                   v-for="item in machineTypeList"
                   :key="item.machineId"
                   :label="item.str"
                   :value="item.machineId">
                 </el-option>
               </el-select>
              </div>
            </div>
            <div class="plqy-main-item">
              <div class="pl-main-name">1901</div>
              <div class="pl-main-value">
                <el-select class='select' style='width:125px' v-model='changemachineId' filterable :placeholder="$t('select')">
                 <el-option
                   v-for="item in machineTypeList"
                   :key="item.machineId"
                   :label="item.str"
                   :value="item.machineId">
                 </el-option>
               </el-select>
              </div>
            </div>

          </div>
        </div> -->
        <div class="pl-tips">{{$t('monitor.bz1')}}<span>{{$t('monitor.bz2')}}</span>{{$t('monitor.bz3')}}</div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='chooseBedType()'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='showSet()'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import {monitorEmptyBeds,machineList,start} from '@/api/monitor'
import {mapGetters,mapActions} from 'vuex'
var that= {};
  export default {
    name: 'qylcgy',
      data(){
          return {
            formData:{
              bedId:'',
              machineId:"",
              patientName:"",
              caseNum:"",
              type:""
            },
            machineTypeList:[],
            bedList:[],
            qyType:0,
            lcMachineTypeId:''
          }
      },
      props:["qylcgyStatus","machineTrans"],
      beforeCreate(){
          that = this;
      },
      mounted:function(){

      },
      methods:{
         ...mapActions("monitor",["getData"]),
         getDevice(){
           console.log(that.formData)
           console.log(that.machineTypeList)
           that.machineTypeList.map(function(item){
             if(item.id === that.formData.machineId){
               that.lcMachineTypeId = item.machineTypeIdChild
             }
           })
           console.log(that.lcMachineTypeId)
         },
        showSet(){
            this.$emit("closeXyy",'离床感应')
          },
        changeBed(data){//床位改变当前设备是否可关联
          console.log(data)
          var arr = []
          arr = this.monitorVOList.filter((item)=>{
             return item.bedId == data
          })
          if(arr.length>0){
            this.formData.patientName = arr[0].patientName
            this.formData.caseNum = arr[0].caseNum
          }else{
            this.formData.patientName = ""
            this.formData.caseNum = ""
          }
        },
        chooseBedType(){//确定启用
        if(!(/^\d+$/.test(this.formData.caseNum))){
           that.error(that.$t('monitor.err.zyh'))
         }else if(this.formData.caseNum.length>6){
           that.error(that.$t('monitor.err.zyhl'))
         }else if(this.formData.patientName.length>20){
           that.error(that.$t('monitor.err.hzxm'))
         }else if(this.formData.bedId&&this.formData.machineId&&this.formData.patientName&&this.formData.caseNum&&this.formData.type){
           start(this.formData).then((res)=>{
              this.$emit("closeXyy",'离床感应')
              console.log(res)
              if(res.code==200&&res.success){
                that.getData(this.departmentId)
                that.success(that.$t('monitor.err.qycg'))
              }else{
                that.error(that.$t('monitor.err.qysb'));
              }
            })
         }else if(!this.formData.bedId){
           that.error(that.$t('monitor.err.cwhnull'))
         }else if(!this.formData.machineId){
           that.error(that.$t('monitor.err.sbhnull'))
         }else if(!this.formData.patientName){
           that.error(that.$t('monitor.err.hzxxnull'))
         }else if(!this.formData.caseNum){
           that.error(that.$t('monitor.err.zyhnull'))
         }
        }
      },
      computed:{
         ...mapGetters("monitor",["departmentId","monitorVOList"])
      },
      components:{
          'PopUp': popUp,
      },
      created(){
        var that = this
        monitorEmptyBeds({
          departmentId:this.departmentId,
          type:this.machineTrans.id
        }).then((res)=>{
          console.log(res)
          if(res.code==200&&res.success){
            this.bedList = res.data
          }
        })
        machineList({
          machineTypeId:this.machineTrans.machineTypeId,
          departmentId:this.departmentId
        }).then((res)=>{
          console.log(res)
          if(res.code==200&&res.success){
            this.machineTypeList= res.data
          }
        })
        this.formData.type = this.machineTrans.id
       console.log(this.machineTrans,this.departmentId)
      }
  }
</script>
<style scoped lang="scss">
.form-item{
    display:flex;
    height:36px;
    margin-top:20px;
}
.plqy-content{
  border-radius:8px;
  margin:0 42px;
  border:2px solid rgba(24,144,255,1);
  .plqy-header{

    display:flex;
    flex-direction: row;
    height:38px;
    background:rgba(24,144,255,1);
    .plqy-header-item{
      width:204px;
      display:flex;
      flex-direction: row;
      .pl-header-name{
        color:#fff;
        width:75px;
        line-height:38px;
        height:100%;
        font-size:13px;
        font-family:PingFangSC-Medium,PingFang SC;
        font-weight:500;
        color:rgba(255,255,255,1);
      }
      .pl-header-value{
        width:127px;
        color:#fff;
        height:100%;
        line-height:38px;
        font-size:13px;
        font-family:PingFangSC-Medium,PingFang SC;
        font-weight:500;
        color:rgba(255,255,255,1);
      }
    }
  }
  .plqy-main{
    width:612px;
    max-height:273px;
    overflow-y: scroll;
    display:flex;
    flex-direction: row;
    flex-wrap: wrap;
    background:rgba(255,255,255,1);
    border-bottom-left-radius: 8px;
    border-bottom-right-radius:8px;
    .plqy-main-item{
      width:204px;
      display:flex;
      flex-direction: row;
      height:55px;
      border-bottom:1px solid #EDEDED;
      .pl-main-name{
        width:75px;
        line-height:55px;
        border-right:1px solid #EDEDED;
      }
      .pl-main-value{
        width:127px;
        padding-top:10px;
        border-right:1px solid #EDEDED;
      }
    }
  }
}
.pl-tips{
  width:612px;
  margin:0 auto;
  text-align:left;
  margin-top:5px;
  line-height:20px;
  font-size:14px;
  font-family:PingFangSC-Regular,PingFang SC;
  font-weight:400;
  color:rgba(35,35,35,1);
  span{
    color:#D0021B;
  }
}
.select-row{
  display:flex;
  flex-direction: row;
  padding-left:42px;
  margin-bottom:16px;
  a{
    display:block;
    // width:81px;
    padding:0 5px;
    height:27px;
    border-radius:17px;
    border:1px solid rgba(24,144,255,1);
    line-height:27px;
    background:#fff;
    color:rgba(24,144,255,1);
    font-size:12px;
    font-family:PingFangSC-Medium,PingFang SC;
    font-weight:500;
    color:rgba(24,144,255,1);
    margin-right:14px;
  }
  .select-active{
    background:rgba(24,144,255,1);
    color:#fff;
  }
}
.content{
  margin:0 42px;
  display:flex;
  flex-direction: column;
  .content-main{
    display:flex;
    flex-direction: row;
    .sb-form{
      width:363px;
      float:left;
      padding:0;
      .form-item-name{
        width:41%;
        span{
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:rgba(51,51,51,1);
          float:right;
          // display:block;
          // line-height:36px;
          display:flex;
          align-items: center;
          justify-content: center;
          height:100%;
        }
      }
      .form-item-value{
        width:59%;
        span{
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:rgba(51,51,51,1);
          float:left;
          display:block;
          line-height:36px;
          i{
            font-style: normal;
            color:#1890FF;
          }
        }
        .select{
          float:left;
        }
      }
    }
    .form-img{
      min-width:200px;
      float:left;
      margin-right:47px;
      display:flex;
      align-items: center;
      img{
        width:202px;
        float:left;
      }
    }
  }
  .tips{
    font-size:14px;
    font-family:PingFangSC-Regular,PingFang SC;
    font-weight:400;
    color:rgba(208,2,27,1);
  }

}
.form-item-value /deep/ .el-input__inner{
  height:36px;
}
.pl-main-value /deep/ .el-input__inner{
  border:0;
  height:36px;
}
::-webkit-scrollbar {
  display:none;
 }
</style>



