<template>
  <div>
      <div class="header">
          <i class="iconfont font-shebei">&#xe62e;</i>
          <div class="header-font">{{$t('monitor.shebei.guaikuang')}}</div>
      </div>
      <div class="content">
          <div class="content-warp">
              <div class="content-project">
                  <div class="project-item">
                      <div class="project-name">{{$t('monitor.shebei.tiwen')}}</div>
                      <div class="project-number">{{tiwenNumber}}/{{tiwen.machineCount}}</div>
                      <div class="project-active" @click="enableDevice(0)">{{$t('monitor.shebei.qiyong')}}</div>
                  </div>
                  <div class="project-item">
                      <div class="project-name">{{$t('monitor.shebei.xindian')}}</div>
                      <div class="project-number">{{xindianNumber}}/{{xindian.machineCount}}</div>
                      <div class="project-active" @click="enableDevice(1)">{{$t('monitor.shebei.qiyong')}}</div>
                  </div>
              </div>
              <div class="content-project">
                  <div class="project-item">
                      <div class="project-name">{{$t('monitor.shebei.xueyang')}} </div>
                      <div class="project-number">{{xueyangNumber}}/{{xueyangyi.machineCount}}</div>
                      <div class="project-active" @click="enableDevice(2)">{{$t('monitor.shebei.qiyong')}}</div>
                  </div>
                  <div class="project-item">
                      <div class="project-name">{{$t('monitor.shebei.lichuang')}}</div>
                      <div class="project-number">{{lichuangNumber}}/{{lichuang.machineCount}}</div>
                      <div class="project-active" @click="enableDevice(3)">{{$t('monitor.shebei.qiyong')}}</div>
                  </div>
              </div>
              <div class="overview-warp" v-for="(item,index) in sheBeiData" :key="index">
                    <div class="overview-left">
                        <div class="overview-mark">{{item.machineTypeName}}</div>
                        <div class="overview-content">{{item.bedName}}&nbsp;&nbsp;{{item.text}}</div>
                    </div>
                    <div class="overview-right"></div>
              </div>
              <div class="overview-warp animated fadeInDown" v-for="(items,indexs) in hideData" :key="indexs+'r'" v-show="moreData">
                    <div class="overview-left">
                        <div class="overview-mark">{{items.machineTypeName}}</div>
                        <div class="overview-content">{{items.bedName}}&nbsp;&nbsp;{{items.text}}</div>
                    </div>
                    <div class="overview-right"></div>
              </div>
              <div class="content-bottom" v-if="sheBeiData.length!=0">
                   <i class="iconfont font-bottom" @click="showData" :class="moreData?'aa':'go'">&#xe625;</i>
              </div>
          </div>
      </div>
  </div>    
</template>
<script>
import { mapState,mapActions} from 'vuex'
export default {
    data(){
        return {
            moreData:false
        }
    },
    computed:{
      ...mapState("login",["language"]),
      ...mapState("monitor",["mainData"]),
      sheBeiData(){
           var arr = this.mainData.machinesInfoVOList.slice(0,2)
           arr.map((item,index)=>{
               if(item.machineType === 0){
                    if(this.language =='en'){
                      arr[index].machineTypeName = "Temperature patch"
                      arr[index].text = "Battery is less than 10%. Please charge the device" 
                    }else{
                      arr[index].machineTypeName = "体温贴"
                      arr[index].text = "电量不足10%，请及时充电"  
                    }
               }else if(item.machineType === 1){
                   if(this.language =='en'){
                        arr[index].machineTypeName = "ECG stickers "
                        arr[index].text = "Battery is less than 10%. Please charge the device"
                    }else{
                        arr[index].machineTypeName = "心电贴 "
                        arr[index].text = "电量不足10%，请及时充电"
                    }
               }else if(item.machineType === 2){
                   if(this.language =='en'){
                       arr[index].machineTypeName = "Oximeter"
                      arr[index].text = "Battery is less than 20%. Please charge the device"
                    }else{
                      arr[index].machineTypeName = "血氧"
                      arr[index].text = "电量不足20%，请及时充电"   
                    }
               }else if(item.machineType === 3){
                   if(this.language =='en'){
                        arr[index].machineTypeName = "Off-bed inductor"
                        arr[index].text = "Battery is less than 10%. Please charge the device"
                    }else{
                        arr[index].machineTypeName = "离床"
                        arr[index].text = "电量不足10%，请及时充电"
                    }
               }else if(item.machineType === 4){
                   if(this.language =='en'){
                        arr[index].machineTypeName = "Temperature patch"
                        arr[index].text = "Low battery, need to replace the base AAA battery"
                    }else{
                        arr[index].machineTypeName = "体温贴"
                        arr[index].text = "电量低，需更换底座AAA电池"
                    }
               }
           })
        return this.mainData.machinesInfoVOList.slice(0,2)
      },
      hideData(){
           var arr2 = this.mainData.machinesInfoVOList.slice(2,5)
           arr2.map((item,index)=>{
               if(item.machineType === 0){
                    if(this.language =='en'){
                      arr2[index].machineTypeName = "Temperature patch"
                      arr2[index].text = "Battery is less than 10%. Please charge the device" 
                    }else{
                      arr2[index].machineTypeName = "体温贴"
                      arr2[index].text = "电量不足10%，请及时充电"  
                    }
               }else if(item.machineType === 1){
                   if(this.language =='en'){
                        arr2[index].machineTypeName = "ECG stickers "
                        arr2[index].text = "Battery is less than 10%. Please charge the device"
                    }else{
                        arr2[index].machineTypeName = "心电贴 "
                        arr2[index].text = "电量不足10%，请及时充电"
                    }
               }else if(item.machineType === 2){
                   if(this.language =='en'){
                       arr2[index].machineTypeName = "Oximeter"
                      arr2[index].text = "Battery is less than 20%. Please charge the device"
                    }else{
                      arr2[index].machineTypeName = "血氧"
                      arr2[index].text = "电量不足20%，请及时充电"   
                    }
               }else if(item.machineType === 3){
                   if(this.language =='en'){
                        arr2[index].machineTypeName = "Off-bed inductor"
                        arr2[index].text = "Battery is less than 10%. Please charge the device"
                    }else{
                        arr2[index].machineTypeName = "离床"
                        arr2[index].text = "电量不足10%，请及时充电"
                    }
               }else if(item.machineType === 4){
                   if(this.language =='en'){
                        arr2[index].machineTypeName = "Temperature patch"
                        arr2[index].text = "Low battery, need to replace the base AAA battery"
                    }else{
                        arr2[index].machineTypeName = "体温贴"
                        arr2[index].text = "电量低，需更换底座AAA电池"
                    }
               }
           })
          return this.mainData.machinesInfoVOList.slice(2,5)
      },
      tiwen(){
          return this.mainData.machineTypeList.filter((item)=>{
             return item.id === 0
          })[0]
      },
      xindian(){
          return this.mainData.machineTypeList.filter((item)=>{
              return item.id === 1
          })[0]
      },
      xueyangyi(){
          return this.mainData.machineTypeList.filter((item)=>{
              return item.id === 2
          })[0]
      },
      lichuang(){
          return this.mainData.machineTypeList.filter((item)=>{
              return item.id === 3
          })[0]
      },
      tiwenNumber(){
          var number = 0
          for(var i=0;i<this.mainData.monitorVOList.length;i++){
              if(this.mainData.monitorVOList[i].machineList){
                 this.mainData.monitorVOList[i].machineList.map((item)=>{
                    if((item.type ==0)&&(item.usageState==1)){
                        number = number+1
                    }
                 })
              }
          }
          return number
      },
      xindianNumber(){
          var number = 0
          for(var i=0;i<this.mainData.monitorVOList.length;i++){
              if(this.mainData.monitorVOList[i].machineList){
                 this.mainData.monitorVOList[i].machineList.map((item)=>{
                    if((item.type ==1)&&(item.usageState==1)){
                        number = number+1
                    }
                 })
              }
          }
          return number
      },
      xueyangNumber(){
         var number = 0
          for(var i=0;i<this.mainData.monitorVOList.length;i++){
              if(this.mainData.monitorVOList[i].machineList){
                 this.mainData.monitorVOList[i].machineList.map((item)=>{
                    if((item.type ==2)&&(item.usageState==1)){
                        number = number+1
                    }
                 })
              }
          }
          return number 
      },
      lichuangNumber(){
         var number = 0
          for(var i=0;i<this.mainData.monitorVOList.length;i++){
              if(this.mainData.monitorVOList[i].machineList){
                 this.mainData.monitorVOList[i].machineList.map((item)=>{
                    if((item.type ==3)&&(item.usageState==1)){
                        number = number+1
                    }
                 })
              }
          }
          return number  
      }
    },
    methods:{
        showData(){
            this.moreData = !this.moreData
        },
        enableDevice(data){//启动设备
           var name = ""
           var arr =  this.mainData.machineTypeList.filter((item)=>{
             if(data===0){
                 name= "体温贴"
             }else if(data===1){
                 name= "心电贴"
             }else if(data===2){
                  name= "血氧仪"
             }else if(data===3){
                  name= "离床感应"
             }
             return  item.id  === data
           })
           this.$emit('setDevice',name,arr[0])
        }
    }
}
</script>
<style scoped>
   .header{
        width:510px;
        height:40px;
        background:rgba(7,34,74,1);
        box-shadow:1px 1px 5px 0px rgba(53,83,228,1);
        border-radius:5px 5px 0px 0px;
        border:1px solid rgba(53,83,228,1);
        display: flex;
        align-items: center;
   }
   .font-shebei{
       color: rgba(63, 201, 255, 1);
       font-size: 20px;
       margin-left: 28px;
   }
   .font-bottom{
       font-size: 9px;
       color: #ffffff;
       cursor: pointer;
   }
   .header-font{
        font-size:18px;
        font-weight:600;
        color:rgba(255,255,255,1);
        line-height:25px;
        letter-spacing:1px;
        margin-left: 16px;
   }
   .content{
       min-height: 198px;
       width:510px;
       min-height:198px;
       border-radius:5px;
       border:1px solid rgba(9,76,138,1);
       border-top: none;
       position: relative;
   }
   .content-warp{
       margin-left: 28px;
       padding-bottom: 38px;
   }
   .content-project{
       display: flex;
       width: 450px;
       justify-content: space-between;
   }
   .content-bottom{
       margin-top: 14px;
       margin-bottom: 11px;
       display: flex;
       justify-content: center;
       width: 100%;
       position: absolute;
       bottom: 2px;
       left: 0px;
   }
   .project-item{
       display: flex;
       margin-top: 13px;
       align-items: center;
   }
   .project-name{
     font-size:20px;
     font-weight:500;
     color:rgba(255,255,255,1);
     line-height:28px;
   }
   .project-number{
      font-size:20px;
      font-weight:500;
      color:rgba(255,255,255,1);
      line-height:28px;
      margin-left: 13px;
   }
   .project-active{
     font-size:20px;
     font-weight:400;
     color:rgba(63,201,255,1);
     line-height:28px;
     margin-left: 13px;
     margin-right: 15px;
     cursor: pointer;
   }
   .overview-warp{
       display: flex;
       width: 450px;
       justify-content: space-between;
       margin-top: 13px;
   }
   .overview-left{
       display: flex;
   }
   .overview-mark{
        min-height:27px;
        border-radius:4px;
        border:1px solid rgba(245,166,35,1);
        line-height: 27px;
        font-size:16px;
        color:rgba(245,166,35,1);
        text-align: center;
        padding: 0 5px;
        min-width: 96px;
        width: auto;
        display: flex;
        align-items: center;
        justify-content: center;
   }
   .overview-content{
        font-size:20px;
        color:rgba(255,255,255,1);
        line-height:28px;
        margin-left: 15px;
   }
   .overview-right{
        font-size:18px;
        font-weight:500;
        color:rgba(255,255,255,1);
        line-height:25px;
   }
   .aa{
        transition: all 1s;
    }
    .go{
        transform:rotate(-180deg);
        transition: all 1s;
    }
</style>


