        <div class="yuanqu">
          <div>{{yuanQu}}</div>
          <i class="iconfont yuanqu-font" :class="selectShow?'xuanzhuan':''" @click="showSelect">&#xe625;</i>
          <div class="select-warp animated fadeIn" v-show="selectShow">
            <div class="select-item" v-for="(item,index) in selectData" :key="index" @click="chooseYuan(index)">{{item.name}}</div>
          </div>
        </div>
        <div class="time hidden-md-and-down">
           <div>
             <div class="date">{{date}}</div>
             <div>{{time}}</div>
