    </div>
    <div class="content-warp">
        <div class="left-warp">
          <div class="item-warp">
            <div class="item  animated fadeInBottomRight" v-for="(item,index) in mainData.monitorVOList" :key="index" @mouseenter="hoverItem(index)" :class="hoverIndex===index?'scraItem':''" @mouseleave="hoverItem('')">
              <div class="item-header">
                <div class="item-header-number" :class="item.errorStatus===3?'redBG':item.errorStatus===2?'orgenBG':item.errorStatus===1?'blueBG':''">{{item.bedName}}</div>
                <div class="item-header-content">
                  <div class="item-header-left">
                    <div class="item-name">{{item.patientName}}</div>
                    <div class="item-chuanghao">{{item.caseNum}}</div>
                    <div class="sleepingTips" v-if="item.sleepingLeaveTimes>=15">{{$t('monitor.content.liChuangShiJian')+item.sleepingLeaveTimes+$t('monitor.content.fenzhong')}}</div>
                  </div>
                  <div v-if="item.sleepingUsage==1&&(item.sleepingState==='')" class="item-data-yichang item-yichang-right">!</div>
                  <el-tooltip class="iconfont item-header-right" effect="dark" :content="$t('monitor.content.liChuangShiJian')+item.sleepingLeaveTimes+$t('monitor.content.fenzhong')" placement="top" v-else-if="item.sleepingState===0" >
                      <i>&#xe645;</i>
                  </el-tooltip>
                  <el-tooltip class="iconfont item-header-right zaichuang" effect="dark" :content="$t('monitor.content.zaichuang')" placement="top" v-else-if="item.sleepingState===1">
                      <i>&#xe646;</i>
                  </el-tooltip>
                </div>
              </div>
              <div class="item-content">
                <div class="item-data-warp">
                  <div class="item-data-left">
                    <i class="iconfont item-font">&#xe634;</i>
                    <div class="item-data-font">
                      <div class="item-data-title">{{$t('monitor.content.tiwen')}}{{sheshi}}</div>
                      <div class="item-data-number" v-if='item.monitorTemperatureVO.temperature' :class="item.monitorTemperatureVO.temperatureAlert===2?'red':item.monitorTemperatureVO.temperatureAlert===1?'orgen':item.monitorTemperatureVO.temperatureAlert===0?'blue':''">
                        {{item.monitorTemperatureVO.temperature}}
                        <div class="item-data-time">{{$t('monitor.content.shichang')}}:{{item.monitorTemperatureVO.useTimes}}</div>
                      </div>
                      <div v-else-if="!item.monitorTemperatureVO.temperature&&(item.monitorTemperatureVO.recordState==0)" class="item-data-yichang">!</div>
                      <div class="item-data-null" v-else></div>
                    </div>
                  </div>
                  <div class="item-data-right">
                    <i class="iconfont item-font1">&#xe626;</i>
                    <div class="item-data-font">
                      <div class="item-data-title">{{$t('monitor.content.xueyang')}}（%）</div>
                      <div class="item-data-number" :class="item.monitorBloodVO.bloodOxygenAlert===2?'red':item.monitorBloodVO.bloodOxygenAlert===1?'orgen':item.monitorBloodVO.bloodOxygenAlert===0?'blue':''" v-if='item.monitorBloodVO.bloodOxygen'>
                         {{item.monitorBloodVO.bloodOxygen}}
                        <div class="item-data-pi">PI:{{item.monitorBloodVO.pi}}</div>
                        <div class="item-data-time">{{$t('monitor.content.shichang')}}:{{item.monitorBloodVO.useTimes}}</div>
                      </div>
                      <div v-else-if="!item.monitorBloodVO.bloodOxygen&&(item.monitorBloodVO.recordState==0)" class="item-data-yichang">!</div>
                      <div class="item-data-null" v-else></div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="item-content noline">
                <div class="item-data-warp">
                  <div class="item-data-left">
                    <i class="iconfont item-font2">&#xe637;</i>
                    <div class="item-data-font">
                      <div class="item-data-title">{{$t('monitor.content.xinlv')}}（bpm）</div>
                      <div class="item-data-null" v-if="(item.monitorHeartRateVO.heart!='')&&(Number(item.monitorHeartRateVO.heart)==0)"></div>
                      <div class="item-data-number" :class="item.monitorHeartRateVO.heartAlert===2?'red':item.monitorHeartRateVO.heartAlert===1?'orgen':item.monitorHeartRateVO.heartAlert===0?'blue':''" v-else-if='item.monitorHeartRateVO.heart'>
                        {{item.monitorHeartRateVO.heart}}
                        <div class="item-data-time">{{$t('monitor.content.shichang')}}:{{item.monitorHeartRateVO.useTimes}}</div>
                      </div>
                      <div v-else-if="!item.monitorHeartRateVO.heart&&(item.monitorHeartRateVO.recordState==0)" class="item-data-yichang">!</div>
                      <div class="item-data-null" v-else></div>
                    </div>
                  </div>
                  <div class="item-data-right">
                    <i class="iconfont item-font3">&#xe638;</i>
                    <div class="item-data-font">
                      <div class="item-data-title">{{$t('monitor.content.huxi')}}（bpm）</div>
                      <div class="item-data-null" v-if="(item.monitorRespiratoryRateVO.respiratory!='')&&(Number(item.monitorRespiratoryRateVO.respiratory)==0)"></div>
                      <div class="item-data-number" :class="item.monitorRespiratoryRateVO.respiratoryAlert===2?'red':item.monitorRespiratoryRateVO.respiratoryAlert===1?'orgen':item.monitorRespiratoryRateVO.respiratoryAlert===0?'blue':''" v-else-if='item.monitorRespiratoryRateVO.respiratory'>
                        {{item.monitorRespiratoryRateVO.respiratory}}
                        <div class="item-data-time">{{$t('monitor.content.shichang')}}:{{item.monitorRespiratoryRateVO.useTimes}}</div>
                      </div>
                      <div v-else-if="!item.monitorRespiratoryRateVO.respiratoryt&&(item.monitorRespiratoryRateVO.recordState==0)" class="item-data-yichang">!</div>
                      <div class="item-data-null" v-else></div>
                    </div>
                  </div>
                </div>
                <div class="item-inner-warp"  :class="hoverIndex===index?'showHeight':''">
                  <el-tooltip class="iconfont inner-font" effect="dark" :content="$t('monitor.content.tooltip.xindian')" placement="bottom" :class="hoverIndex===index?'showIconHeight':''">
                      <i @click="openBullet('心电图',item)">&#xe64a;</i>
                  </el-tooltip>
                  <el-tooltip class="iconfont inner-font" effect="dark" :content="$t('monitor.content.tooltip.lishi')" placement="bottom" :class="hoverIndex===index?'showIconHeight':''">
                      <i @click="openBullet('历史记录',item)">&#xe649;</i>
                  </el-tooltip>
                  <el-tooltip class="iconfont inner-font" effect="dark" :content="$t('monitor.content.tooltip.shebei')" placement="bottom" :class="hoverIndex===index?'showIconHeight':''">
                      <i @click="openBullet('更换设备',item)">&#xe64d;</i>
                  </el-tooltip>
                  <el-tooltip class="iconfont inner-font" effect="dark" :content="$t('monitor.content.tooltip.chuangwei')" placement="bottom" :class="hoverIndex===index?'showIconHeight':''">
                      <i @click="openBullet('更换床位',item)">&#xe64c;</i>
                  </el-tooltip>
                  <el-tooltip class="iconfont inner-font" effect="dark" :content="$t('monitor.content.tooltip.jiance')" placement="bottom" :class="hoverIndex===index?'showIconHeight':''">
                      <i @click="openBullet('停止监测',item)">&#xe64b;</i>
                  </el-tooltip>
                </div>
              </div>
            </div>
            <i></i><i></i><i></i><i></i><i></i>
            <div class="left-bottom">{{$t('monitor.content.beizhu')}}</div>
          </div>
        </div>
        <div class="right-warp" v-show="show">
          <div class="right-content">
            <div class="right-content-warp">
              <dSheBei @setDevice="setDevice"></dSheBei>
              <dYi class="right-content-yichang"></dYi>
            </div>
            <div class="buttun" @click="hidden">
              <i class="iconfont buttun-font">&#xe628;</i>
            </div>
          </div>
        </div>
        <div class="buttun2" @click="hidden" v-show="!show">
           <i class="iconfont buttun-font">&#xe627;</i>
        </div>
    </div>
    <!-- 三个历史记录 -->
    <lsjl :lsjlStatus='lsjlStatus' @closeIitem="closeIitem" v-if="lsjlStatus" :transData="transData"></lsjl>
    <!-- 心电历史记录 -->
    <xdjl :xdjlStatus='xdjlStatus' v-if="xdjlStatus" @closeIitem="closeIitem" :transData="transData"></xdjl>
    <!-- 停止监测 -->
    <tzjc :tzjcStatus='tzjcStatus' @closeIitem="closeIitem" :transData="transData" v-if="tzjcStatus"></tzjc>
    <!-- 更换床位 -->
    <ghcw :ghcwStatus='ghcwStatus' @closeIitem="closeIitem" v-if="ghcwStatus" :transData="transData"></ghcw>
    <!-- 更换设备 -->
    <ghsb :ghsbStatus='ghsbStatus' @closeIitem="closeIitem" v-if="ghsbStatus" :transData="transData"></ghsb>
    <!-- 启用体温贴 -->
    <qytwt :qytwtStatus='qytwtStatus' @closeXyy="closeXyy" :machineTrans="machineTrans" v-if="qytwtStatus"></qytwt>
    <!-- 启用 -->
    <qyxdt :qyxdtStatus='qyxdtStatus' @closeXyy="closeXyy" :machineTrans="machineTrans" v-if="qyxdtStatus"></qyxdt>
    <qylcgy :qylcgyStatus='qylcgyStatus' @closeXyy="closeXyy" :machineTrans="machineTrans" v-if="qylcgyStatus"></qylcgy>
    <qyxyy :qyxyyStatus='qyxyyStatus' @closeXyy="closeXyy" :machineTrans="machineTrans" v-if="qyxyyStatus"></qyxyy>
  </div>
</template>

<script>
import { mapState,mapActions,mapGetters,mapMutations} from 'vuex'
var that= {};
import dHeader from "../components/header.vue"
import tzjc from "../components/monitor/tzjc.vue"
import lsjl from "../components/monitor/lsjl.vue"
import xdjl from "../components/monitor/xdjl.vue"
import ghcw from "../components/monitor/ghcw.vue"
import ghsb from "../components/monitor/ghsb.vue"
import qytwt from "../components/monitor/qytwt.vue"
import qyxdt from "../components/monitor/qyxdt.vue"
import qylcgy from "../components/monitor/qylcgy.vue"
import qyxyy from "../components/monitor/qyxyy.vue"

import dSheBei from "../components/shebei.vue"
import dYi from "../components/yichang.vue"
import {getDepartmentList,time,hospitalName} from '@/api/monitor'
export default {
  name: 'HelloWorld',
  data () {
    return {
      sheshi:'（℃）',
      transData:"",//每一项转移的数据
      show:true,
      show2:true,
      lsjlStatus:false,
      xdjlStatus:false,
      tzjcStatus:false,
      ghcwStatus:false,
      ghsbStatus:false,
      qytwtStatus:false,
      qyxdtStatus:false,
      qylcgyStatus:false,
      qyxyyStatus:false,
      selectShow:false,
      selectData:[],
      yuanQu:"",
      yuanQuId:"",
      hoverIndex:"",
      machineTypeList:[],
      machineTrans:"",//启用设备所要输出的值
      changemachineId:'',
      date:"",
      week:"",
      time:"",
      label:"",//获取时间定时器
      dataLabel:"",//获取数据定时器
      serverTime:"",//服务器时间戳
      yiyuanName:""//医院名称
    }
  },
  beforeCreate(){
      that = this;
  },
  mounted(){
    this.setIntervalGetDate();
    console.log(this.mainData)
    // that.getDepartmentListFn()
  },
  beforeDestroy(){
    this.clearIntervalGetDate()
  },
  computed:{
  ...mapState("monitor",["mainData","selectIndex","selectTem"]),
  ...mapState("login",["language"]),
  ...mapGetters("login",["useDepartId","role"])
  },
  methods:{
    ...mapMutations("monitor",["setFilterBedId"]),
    ...mapActions("monitor",["getData","initialData","changeSelect","getDepartmentListA"]),
    hidden(){
      this.show = !this.show
      console.log(this.show)
    },
    headerHidden(){
      this.show2 = !this.show2
      console.log(this.show2)
    },
    enter(){
      this.show2 = true
    },
    showSelect(){
      this.selectShow = !this.selectShow
    },
    chooseYuan(index){
      this.changeSelect(index)
      this.yuanQuId = this.selectData[index].departmentId
      this.yuanQu = this.selectData[index].name
      this.selectShow = false
      that.setFilterBedId({startBedId:null,endBedId:null})//初始筛选床位
      that.accept(this.yuanQuId)
    },
    hoverItem(index){
      this.hoverIndex = index
    },
    getDates(){//获取系统时间
      that.serverTime = that.serverTime + 1000
      var dates = new Date(that.serverTime);
      var Month= dates.getMonth()+1; //月
      Month= Month< 10 ? "0"+Month:Month;
      if(that.language=='en'){
        this.date = Month+'-'+dates.getDate()+'-'+dates.getFullYear();
        this.week = ["Sunday","Monday","Tuesday","Wednesday","Thursday","Friday","Saturday"][dates.getDay()]
      }else{
        this.date = dates.getFullYear() +'-' + Month +'-' + dates.getDate()
        this.week ='星期' + '日一二三四五六'.charAt(dates.getDay())
      }
      var second = dates.getSeconds(); //秒
      var minute = dates.getMinutes();//分
      second = second < 10 ?"0"+second:second;
      minute = minute < 10 ?"0"+minute:minute;
      this.time = dates.getHours() +": " + minute +': ' +second
    },
    setIntervalGetDate(){//动态获取
      var that = this
      this.label = setInterval(that.getDates, 1000);
      this.dataLabel = setInterval(()=>{that.getData(that.yuanQuId)}, 60000)
    },
    clearIntervalGetDate(){//结束清除定时器
      clearInterval(this.label);
      clearInterval(this.dataLabel);
    },
    setDevice(data,arr){//打开弹框
       console.log(data,arr)
       if(data=='血氧仪'){
         this.qyxyyStatus = true
       }else if(data=='体温贴'){
          this.qytwtStatus = true
       }else if(data=='心电贴'){
         this.qyxdtStatus = true
       }else if(data=='离床感应'){
         this.qylcgyStatus = true
       }
       this.machineTrans = arr
    },
    closeXyy(data){//关闭弹框
       if(data=='血氧仪'){
         this.qyxyyStatus = false
       }else if(data=='体温贴'){
          this.qytwtStatus = false
       }else if(data=='心电贴'){
         this.qyxdtStatus = false
       }else if(data=='离床感应'){
         this.qylcgyStatus = false
       }
    },
    openBullet(data,trans){
      console.log(data)
      if(trans.bedId=="null"){
         that.error(that.$t('monitor.err.wcwxx'))
      }else{
          if(data=='历史记录'){
            this.lsjlStatus = true
          }else if(data=='更换设备'){
              this.ghsbStatus = true
          }else if(data=='更换床位'){
            this.ghcwStatus = true
          }else if(data=='停止监测'){
            this.tzjcStatus = true
          }else if(data=='心电图'){
            this.xdjlStatus = true
          }
      }
      this.transData = trans
    },
    closeIitem(data){//关闭内容弹框
       if(data=='历史记录'){
         this.lsjlStatus = false
       }else if(data=='更换设备'){
          this.ghsbStatus = false
       }else if(data=='更换床位'){
         this.ghcwStatus = false
       }else if(data=='停止监测'){
         this.tzjcStatus = false
       }else if(data=="停止心电图"){
         this.xdjlStatus = false
       }
    },
    accept(departmentId){//接受数据
        this.getData(departmentId)
      // this.initialData(departmentId).then((res)=>{
      //   this.getData(departmentId)
      // })
    }
  },
  watch:{
    selectTem(o){
      if(o){
        this.sheshi = "（℃）"
      }else{
         this.sheshi = " (℉)"
      }
    }
  },
  created(){
   if(!that.selectTem){
       that.sheshi = " (℉)"
   }
   hospitalName().then((res)=>{
     console.log(res)
     if(res.success&&res.code==200){
       this.yiyuanName = res.data
     }
   })
   time().then((res)=>{
      that.serverTime = res.data
   })
   getDepartmentList().then((res)=>{//通过科室和角色信息截取数据
     console.log(res)
     var arr = []
     if(this.role === 2){
        arr = res.data.filter((item)=>{
         return item.departmentId == this.useDepartId
        })
        that.selectData = arr
     }else{
        arr = res.data
        that.selectData = arr
     }
     that.getDepartmentListA(arr);//存储到全局
     that.yuanQu = arr[that.selectIndex].name
     that.yuanQuId = arr[that.selectIndex].departmentId
     that.accept(that.yuanQuId)
   })
  },
  components:{
      tzjc,
      lsjl,
      xdjl,
      ghcw,
      ghsb,
      qytwt,
      qyxdt,
      qylcgy,
      qyxyy,
      dHeader,
      dSheBei,
      dYi
  }
}
</script>

