    }
  }
  .item{
        width:298px;
        height: 230px;
        margin-bottom: 22px;
        background:rgba(18,37,64,1);
        border-radius:4px;
        position: relative;
        border:1px solid rgba(9,76,138,1);
        .item-inner-warp{
          width: 300px;
          position: absolute;
          top: 228px;
          left: -1px;
          z-index: 10;
          height: 0px;
          padding: 0 16px 0 20px;
          box-sizing: border-box;
          display: flex;
          justify-content: space-between;
          align-items: center;
        }
        .inner-font{
          font-size: 30px;
