          background:rgba(18,37,64,1);
          box-shadow:6px 6px 15px 0px rgba(12,25,45,1);
          border-bottom-left-radius: 4px;
          border-bottom-right-radius: 4px;
          border:1px solid rgba(9,76,138,1);
        }
        .showIconHeight{
          opacity: 1;
          transition: opacity 1s;
          transition-delay: 0.2s;
        }
        .item-header{
          height: 50px;
          border-bottom: 1px solid rgba(74, 144, 226, 0.3);
          display: flex;
        }
        .item-header-number{
          min-width:59px;
          height:50px;
          background:rgba(143,158,178,1);
          border-radius:0px 0px 4px 0px;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size:20px;
          font-weight:400;
          color:rgba(255,255,255,1);
          white-space: nowrap;
          overflow: hidden;
          text-align: center;
          padding: 0 10px;
          box-sizing: border-box;
        }
        .blueBG{
            background:rgba(47,84,235,1);
        }
        .orgenBG{
          background:#FA9D00;
        }
        .redBG{
          background:rgba(208,2,27,1);
        }
        .item-header-left{
          margin-left: 13px;
          display: flex;
          .item-name{
            font-size:18px;
            font-weight:500;
            color:rgba(255,255,255,1);
            line-height:50px;
            max-width: 100px;
            max-height: 50px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          .item-chuanghao{
            font-size:14px;
            font-weight:400;
            color:rgba(255,255,255,1);
            line-height:50px;
            margin-left: 13px;
          }
        }
        .item-header-right{
          color: rgba(208, 2, 27, 1);
          font-size: 40px;
          margin-top: 10px;
          cursor: pointer;
        }
        .sleepingTips{
            position: absolute;
            color: red;
            font-size: 12px;;
            right: 0;
            top: 36px;
        }
        .zaichuang{
            color: #5AF5D2;
        }
        .item-header-content{
          width: 239px;
          height: 50px;
          display: flex;
          align-items: center;
          justify-content: space-between;
        }
        .item-content{
          display: flex;
          align-items: center;
          justify-content: center;
          border-bottom: 1px solid rgba(74, 144, 226, 0.3);
          height: 89px;
          .orgen{
            color:#FA9D00;
          }
          .red{
            color:rgba(208,2,27,1);
          }
        }
        .noline{
          border:none;
          min-width: 280px;
        }
        .item-data-warp{
          min-width: 270px;
          height: 60px;
          display: flex;
          align-items: center;
          .item-data-left{
            min-width: 130px;
            height: 60px;
            border-right: 1px solid rgba(74, 144, 226, 0.3);
            display: flex;
             align-items: center;
          }
          .item-data-right{
            min-width: 130px;
            height: 60px;
            display: flex;
             align-items: center;
          }
        }
        .item-font{
          font-size: 41px;
          color: rgba(67,87,168,1);
        }
        .item-font1{
          font-size: 29px;
          color: rgba(67,87,168,1);
          margin-left: 14px;
          margin-right: 8px;
        }
        .item-font2{
          font-size: 26px;
          color: rgba(67,87,168,1);
          margin-right: 6px;
          margin-left: 6px;
        }
        .item-font3{
          font-size: 26px;
          color: rgba(67,87,168,1);
          margin-right: 8px;
          margin-left: 14px;
        }
        .item-data-font{
          height: 58px;
          position: relative;
        }
        .item-data-pi{
            font-size:14px;
            font-weight:400;
            color:rgba(226,240,255,0.6);
            line-height:20px;
            text-shadow:6px 6px 15px rgba(12,25,45,1);
            margin-top: 10px;
            margin-left: 2px;
         }
         .item-data-time{
           position: absolute;
           font-size:10px;
           font-weight:400;
           color:rgba(140,155,188,1);
           line-height:14px;
           bottom: -10px;
           left: -47px;
           width: 150px;
         }
        .item-data-title{
          font-size:14px;
          font-weight:400;
          color:rgba(226,240,255,0.6);
          line-height:20px;
          margin-top: 4px;
        }
        .item-data-number{
          font-size:24px;
          font-weight:500;
          color:rgba(255,255,255,1);
          line-height: 33px;
          display: flex;
        }
        .item-data-yichang{
          color: red;
          font-size: 22px;
          text-indent: 10px;
        }
        .item-data-null{
          width:21px;
          height:4px;
          background:rgba(143,158,178,1);
          border-radius:2px;
          margin-top: 9px;
        }
  }
  .right-content-yichang{
    margin-top: 19px;
  }
  .item-warp > i {
    width: 298px;
   }
  ::-webkit-scrollbar {
    width:13px;
    background:rgba(255,255,255,0.16);
    border-radius:7px;
  }
  ::-webkit-scrollbar-thumb{
    width:7px;
    background:rgba(255,255,255,0.16);
    border-radius:7px;
  }
  .form-item-right /deep/ .el-input__inner{
    height:36px;
  }
  .form-item-value /deep/ .el-input__inner{
    height:36px;
  }
  /deep/ .el-tooltip__popper{
     z-index: 1 !important;
  }
  .scraItem{
        transform:scale(1.06);
        position: relative;
        z-index: 100;
  }

</style>



