<template>
  <PopUp :flow='ghcwStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='showSet()'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe64c;</i>
          <span class="title-span">{{$t('monitor.ghcw')}}</span>
        </div>
        <div class="content">
          <div class="form">
            <div class="form-item">
              <div class="form-item-left">
                <span>{{$t('monitor.hzxx')}}：</span>
              </div>
              <div class="form-item-right">
                <span>{{transData.patientName}} {{transData.caseNum}}</span>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-left">
                <span>{{$t('monitor.ycwh')}}：</span>
              </div>
              <div class="form-item-right">
                <span class='ysbh'>{{transData.bedName}}</span>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-left">
                <span>{{$t('monitor.xcwh')}}：</span>
              </div>
              <div class="form-item-right">
                <el-select class='select' style='width:145px' v-model='changebedId' filterable :placeholder="$t('message.select')">
                 <el-option
                   v-for="item in bedList"
                   :key="item.bedId"
                   :label="item.name"
                   :value="item.bedId">
                 </el-option>
               </el-select>
              </div>
            </div>

          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='chooseBedType()'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='showSet()'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import { mapState,mapActions} from 'vuex'
import popUp from '../../components/popUp'
import {emptyBeds,changebed} from '@/api/monitor'
var that= {};
  export default {
    name: 'tjzc',
      data(){
          return {
            changebedId:'',
            bedList:[],
          }
      },
      props:["ghcwStatus","transData"],
      beforeCreate(){
          that = this;
      },
      mounted(){
      },
      methods:{
        ...mapActions("monitor",["refreshData"]),
        showSet(){
            this.$emit("closeIitem",'更换床位')
        },
        chooseBedType(){//点击确认
           if(this.changebedId){
             changebed({
               newBedId:this.changebedId,
               currentBedId:this.transData.bedId
             }).then((res)=>{
               console.log(res)
               if(res.success&&res.code==200){
                 that.$emit("closeIitem",'更换床位')
                 that.refreshData()
                 that.success(that.$t('genhuan.success'))
               }else{
                  that.error(that.$t('genhuan.error'))
               }
             })
           }
        }
      },
      computed:{
      ...mapState("monitor",["departmentList","selectIndex"])
      },
      components:{
          'PopUp': popUp,
      },
      created(){
         var that = this
         console.log(this.departmentList[this.selectIndex].departmentId)
         console.log(this.transData)
         emptyBeds({departmentId:this.departmentList[this.selectIndex].departmentId}).then((res)=>{
           console.log(res)
           if(res.code==200&&res.success){
             that.bedList = res.data
           }
         })
      }
  }
</script>
<style scoped lang="scss">
.content{
  margin:0 42px;
  flex-direction: column;
  .form{
    width:612px;
    padding-bottom:20px;
    .form-item{
      display:flex;
      height:36px;
      margin-top:20px;
      .form-item-left{
        width:42%;
        span{
          float:right;
          display:block;
          line-height:36px;
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:600;
          color:#333;
        }
      }
      .form-item-right{
        width:58%;
        span{
          float:left;
          line-height:36px;
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:#666666;
        }
        .ysbh{
          color:#1890FF;
        }
        .select{
          float:left;
        }
      }
    }
  }
}
</style>



